import Vue from 'vue'
import App from './App'
import Axios from 'axios'
import store from './store'
import router from './router'
import vuetify from './plugins/vuetify';
import VueCurrencyFilter from 'vue-currency-filter'
Vue.use(VueCurrencyFilter,
  {
    // symbol : 'Rp.',
    thousandsSeparator: '.',
    fractionCount: 2,
    fractionSeparator: ',',
    symbolPosition: 'front',
    symbolSpacing: false
  })
Vue.prototype.$http = Axios;
// (function() {
// const token = localStorage.getItem('token')
// if (token) {
//   Vue.prototype.$http.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
// }
// })();

// Untuk menset header setiap kali request melalui Axios
Axios.interceptors.request.use(
  (config) => {
    let token = localStorage.getItem('token')

    if (token) {
      config.headers['Authorization'] = `Bearer ${ token }`
    }

    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)


Axios.interceptors.response.use((config) => {
  let token = localStorage.getItem('token')

  if (token) {
    config.headers['Authorization'] = `Bearer ${ token }`
  }

  return config
}, function (error) {
  // console.log(error.response.status)
  if (error.response.status === 401) {
    store.dispatch('logout')
    router.push('/login')
  }
  return Promise.reject(error)
})
// Vue.prototype.$apiUrl = 'http://localhost:8000/api/';
// Vue.prototype.$publicUrl = 'http://localhost:8000/';
Vue.prototype.$apiUrl = 'http://192.168.20.11/Niagara_Backend/api/';
Vue.prototype.$publicUrl = 'http://192.168.20.11/Niagara_Backend/';
Vue.config.productionTip = false
new Vue({
 render: h => h(App),
 router,store,
 vuetify,
 components: { App }
}).$mount('#app')